        'TagText': '167',
        'Indent': '0',
        'Position': '10',
        'Reqd': '0',
        'Description': 'It is recommended that CFICode be used instead of SecurityType for non-Fixed Income instruments.\nRequired for Fixed Income. Refer to Volume 7 - Fixed Income\nFutures and Options should be specified using the CFICode[461] field instead of SecurityType[167] (Refer to Volume 7 - Recommendations and Guidelines for Futures and Options Markets.)',
