        'ComponentID': '1003',
        'TagText': '200',
        'Indent': '0',
        'Position': '12',
        'Reqd': '0',
        'Description': 'Specifies the month and year of maturity. Applicable for standardized derivatives which are typically only referenced by month and year (e.g. S&P futures). Note MaturityDate (a full date) can also be specified.',
