        'TagText': '541',
        'Indent': '0',
        'Position': '13',
        'Reqd': '0',
        'Description': 'Specifies date of maturity (a full date).  Note that standardized derivatives which are typically only referenced by month and year (e.g. S&P futures).may use MaturityMonthYear and/or this field.\nWhen using MaturityMonthYear, it is recommended that markets and sell sides report the MaturityDate on all outbound messages as a means of data enrichment.\nFor NDFs this represents the fixing date of the contract.',
